import React from 'react'
import { connect } from 'react-redux'

import Filter from '../components/Filter'
import { setDispatchFilterParams } from '../actions/DispatchActions'
import { fetchGroupsList } from '../actions/GroupsListActions'

const mapStateToProps = state => ({
  user: state.session.user,
  params: state.dispatch.filter.params,
  addressList: state.addressList.list,
  addressListPending: state.addressList.listPending,
  addressListError: state.addressList.listError,
  groupsList: state.groupsList.list,
  groupsListPending: state.groupsList.listPending,
  groupsListError: state.groupsList.listError,
})

const mapDispatchToProps = dispatch => ({
  setParams: params => dispatch(setDispatchFilterParams(params)),
  fetchGroupsList: params => dispatch(fetchGroupsList(params)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Filter)
