import React from 'react'
import { withStyles } from '@material-ui/styles'

import Tooltip from '@material-ui/core/Tooltip'
import LockIcon from '@material-ui/icons/LockOutlined'
//import SvgIcon from '@material-ui/core/SvgIcon'
//import MdiIcon from '@mdi/react'
//import { mdiLockOpen } from '@mdi/js'

import createSvgIcon from '@material-ui/icons/utils/createSvgIcon'

const LockOpenIcon = createSvgIcon(
  <path d="M18,8A2,2 0 0,1 20,10V20A2,2 0 0,1 18,22H6C4.89,22 4,21.1 4,20V10A2,2 0 0,1 6,8H15V6A3,3 0 0,0 12,3A3,3 0 0,0 9,6H7A5,5 0 0,1 12,1A5,5 0 0,1 17,6V8H18M12,17A2,2 0 0,0 14,15A2,2 0 0,0 12,13A2,2 0 0,0 10,15A2,2 0 0,0 12,17Z" />,
  'LockOpen'
)

class ThingGuard extends React.PureComponent {
  isActive() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.guard === true || thing.guard === 'true'
    } else {
      return thingShadow.state.reported.guard === true
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
      whenActive,
      whenInactive,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon &&
          (hideLabel || hideValue ? (
            <Tooltip
              title={
                <span className={classes.tooltip}>
                  Охрана объекта: <span>{this.renderValue()}</span>
                </span>
              }
            >
              {this.renderIcon()}
            </Tooltip>
          ) : (
            this.renderIcon()
          ))}
        {!hideLabel && <span className={classes.label}>Охрана объекта</span>}
        {!hideValue && this.renderValue()}
        {whenActive && this.isActive() && whenActive}
        {whenInactive && !this.isActive() && whenInactive}
      </span>
    )
  }

  renderIcon() {
    const { classes } = this.props
    return this.isActive() ? (
      <LockIcon className={`${classes.icon} ${classes.icon_textSecondary}`} />
    ) : (
      <LockOpenIcon className={`${classes.icon} ${classes.icon_textPrimary}`} />
    )
  }

  renderValue() {
    const { classes } = this.props
    return this.isActive() ? (
      <span className={classes.value}>Включена</span>
    ) : (
      <strong className={classes.value}>Отключена</strong>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
  tooltip: {
    fontWeight: 'normal',
    '& > span': {
      textTransform: 'lowercase',
    },
  },
})

export default withStyles(styles)(ThingGuard)
