import React from 'react'
import { withStyles } from '@material-ui/styles'

class ThingIdentity extends React.PureComponent {
  render() {
    const { classes, thing, addressById } = this.props
    if (!thing) {
      return null
    }
    return (
      <span className={classes.root}>
        <span>{thing.device}</span>
        @
        {!!thing.address_id &&
        !!addressById &&
        !!addressById[thing.address_id] ? (
          <span>{addressById[thing.address_id].name.S}</span>
        ) : (
          <span>Неизвестно</span>
        )}
      </span>
    )
  }
}

const styles = theme => ({
  root: {},
})

export default withStyles(styles)(ThingIdentity)
