import React from 'react'
import { withStyles } from '@material-ui/styles'

import moment from 'moment'
import 'moment/locale/ru'

import ScheduleIcon from '@material-ui/icons/Schedule'

class ThingUpdated extends React.PureComponent {
  getValue() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return moment(Number(`${thing.msgtime}000`)).format('DD.MM.YY HH:mm')
    } else {
      return moment(Number(`${thingShadow.state.reported.msgtime}000`)).format(
        'DD.MM.YY HH:mm'
      )
    }
  }

  getValueDuration() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return moment
        .duration(Number(`${thing.msgtime}000`) - new Date().getTime())
        .locale('ru')
        .humanize(true)
    } else {
      return moment
        .duration(
          Number(`${thingShadow.state.reported.msgtime}000`) -
            new Date().getTime()
        )
        .locale('ru')
        .humanize(true)
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
      hideValueDuration = false,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon && (
          <ScheduleIcon
            className={`${classes.icon} ${classes.icon_textSecondary}`}
          />
        )}
        {!hideLabel && <span className={classes.label}>Обновлено</span>}
        {!hideValue && <span className={classes.value}>{this.getValue()}</span>}
        {!hideValueDuration && (
          <span className={classes.valueDuration}>
            {this.getValueDuration()}
          </span>
        )}
      </span>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
})

export default withStyles(styles)(ThingUpdated)
