import { AWS_CONFIG } from '../settings/aws/AwsConfig'
import * as AWS from 'aws-sdk'
import * as getCSV from 'get-csv'
import { getCsvShka } from '../services/CsvShkaService'

export const THING_LIST_FETCH = 'THING_LIST_FETCH'

export function fetchThingList() {
  return async (dispatch, getState) => {
    dispatch({
      type: THING_LIST_FETCH,
      status: 'pending',
    })
    try {
      const {
        session: { user },
      } = getState()
      AWS.config.update({ region: AWS_CONFIG.region })
      const loginCredentialsProvider =
        'cognito-idp.' +
        AWS_CONFIG.region +
        '.amazonaws.com/' +
        AWS_CONFIG.userPoolId
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: AWS_CONFIG.identityPoolId,
        Logins: {
          [loginCredentialsProvider]: user.credentials.idToken,
        },
      })
      const Iot = new AWS.Iot(user.credentials)
      const IotAnalytics = new AWS.IoTAnalytics()
      const data = await getCsvShka(IotAnalytics, 'st_eye_dataset_dispatch')
      const dataUri = data['entries'][0]['dataURI']
      const thingsList = await getCSV(dataUri)
      dispatch({
        type: THING_LIST_FETCH,
        status: 'success',
        payload: thingsList,
      })
    } catch (err) {
      dispatch({
        type: THING_LIST_FETCH,
        status: 'failure',
        payload: err,
      })
    }
  }
}
