import React from 'react'
import { withStyles } from '@material-ui/styles'

import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import SensorStatus from '../Sensor/SensorStatus'

class ThingSensors extends React.PureComponent {
  render() {
    const { classes, thing, thingShadow } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <div className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Статус</TableCell>
              <TableCell>Название</TableCell>
              <TableCell>Роль</TableCell>
              <TableCell>Значение</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {!!thingShadow &&
              thingShadow.state.reported.ain &&
              thingShadow.state.reported.ain.map((sensor, i) => (
                <TableRow key={i}>
                  <TableCell>
                    <SensorStatus sensor={sensor} />
                  </TableCell>
                  <TableCell>{sensor.name}</TableCell>
                  <TableCell>{sensor.role}</TableCell>
                  <TableCell>{sensor.value}</TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </div>
    )
  }
}

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  table: {
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(3),
    '& thead th': {
      padding: theme.spacing(0, 2),
      lineHeight: '24px',
      '&:first-child': {
        paddingLeft: 0,
      },
    },
    '& tbody th, & tbody td': {
      padding: theme.spacing(1.5, 2),
      lineHeight: '24px',
      '&:first-child': {
        paddingLeft: 0,
      },
    },
    '& > tbody > tr:last-child th, & > tbody > tr:last-child td': {
      borderBottom: 0,
    },
  },
})

export default withStyles(styles)(ThingSensors)
