import { DISPATCH_FILTER_PARAMS_SET } from '../actions/DispatchActions'

const initialState = {
  filter: {
    params: {
      address: null,
      noPower: true,
      noConnection2h: false,
      noConnection24h: false,
      noEthernet: false,
      lowGsm: false,
      statusNormal: false,
      statusAlmin: false,
      statusAlmaj: false,
      activeEthernet: false,
      activeGsm: false,
      noConnection: false,
    },
  },
}

export default (state = initialState, action) => {
  switch (action.type) {
    case DISPATCH_FILTER_PARAMS_SET: {
      const { params } = action
      return {
        ...state,
        filter: {
          ...state.filter,
          params,
        },
      }
    }

    default: {
      return state
    }
  }
}
