import React from 'react'
import { Link } from 'react-router-dom'

class AnalystGroupListItem extends React.PureComponent {
  render() {
    const { index, style, data: { classes, total, groups } = {} } = this.props
    if (index === 0) {
      return (
        <div className={`${classes.item}`} style={style}>
          <div className={`${classes.itemColumn} ${classes.itemColumn_title}`}>
            Всего
          </div>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_count}`}
            to={`/dispatch`}
          >
            {total.count}
          </Link>
          <div className={`${classes.itemColumn} ${classes.itemColumn_error}`}>
            {total.errorPercentage}%
          </div>
          <div
            className={`${classes.itemColumn} ${classes.itemColumn_service}`}
          ></div>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?noConnection=true`}
          >
            {total.noConnectionCount}
          </Link>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?statusNormal=true`}
          >
            {total.statusNormalCount}
          </Link>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?statusAlmin=true`}
          >
            {total.statusAlminCount}
          </Link>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?statusAlmaj=true`}
          >
            {total.statusAlmajCount}
          </Link>
        </div>
      )
    } else {
      const group = groups[index - 1]
      return (
        <div className={`${classes.item}`} style={style}>
          <div className={`${classes.itemColumn} ${classes.itemColumn_title}`}>
            Группа {group.id}
          </div>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_count}`}
            to={`/dispatch?address=${group.id}`}
          >
            {group.count}
          </Link>
          <div className={`${classes.itemColumn} ${classes.itemColumn_error}`}>
            {group.errorPercentage}%
          </div>
          <div
            className={`${classes.itemColumn} ${classes.itemColumn_service}`}
          ></div>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?address=${group.id}&noConnection=true`}
          >
            {group.noConnectionCount}
          </Link>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?address=${group.id}&statusNormal=true`}
          >
            {group.statusNormalCount}
          </Link>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?address=${group.id}&statusAlmin=true`}
          >
            {group.statusAlminCount}
          </Link>
          <Link
            className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            to={`/dispatch?address=${group.id}&statusAlmaj=true`}
          >
            {group.statusAlmajCount}
          </Link>
        </div>
      )
    }
  }
}

export default AnalystGroupListItem
