import React from 'react'
import { withStyles } from '@material-ui/styles'

import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Chip from '@material-ui/core/Chip'
import Dialog from '@material-ui/core/Dialog'
import AppBar from '@material-ui/core/AppBar/AppBar'
import Container from '@material-ui/core/Container'
import DialogContent from '@material-ui/core/DialogContent'
import ThingIdentity from './Thing/ThingIdentity'
//import MdiIcon from '@mdi/react'
//import { mdiChip, mdiLockOpen } from '@mdi/js'

import CloseIcon from '@material-ui/icons/Close'
import { IconButton } from '@material-ui/core'
//import DialogActions from '@material-ui/core/DialogActions'
//import Button from '@material-ui/core/Button'

class Dispatch extends React.Component {
  state = {
    thing: null,
  }

  render() {
    const { classes } = this.props
    const {
      filterComponent: Filter,
      eventListComponent: EventList,
      thingListComponent: ThingList,
      thingDetailsComponent: ThingDetails,
    } = this.props
    return (
      <React.Fragment>
        <Box className={classes.root} display="flex" flexGrow={1}>
          <Filter />
          <Box flexGrow={1} display="flex" flexDirection="column" pt={2} pb={2}>
            <Paper
              component={Box}
              height="62%"
              display="flex"
              flexDirection="column"
              mb={2}
            >
              <Toolbar>
                <Typography variant="h6">Текущие показания</Typography>
              </Toolbar>
              <ThingList
                onClickThing={thing => {
                  this.setState({ thing })
                }}
              />
            </Paper>
            <Paper
              component={Box}
              flexGrow={1}
              display="flex"
              flexDirection="column"
            >
              <Toolbar>
                <Typography variant="h6">Журнал событий</Typography>
                <div>
                  <Chip className={classes.chip} label="Все события" />
                  <Chip className={classes.chip} label="Связь" />
                  <Chip className={classes.chip} label="Охрана" />
                  <Chip className={classes.chip} label="Оборудование" />
                </div>
              </Toolbar>
              <Box flexGrow={1} position="relative">
                <EventList />
              </Box>
            </Paper>
          </Box>
          <Dialog
            classes={{
              container: classes.dialogContainer,
              paper: classes.dialogPaper,
            }}
            open={!!this.state.thing}
            onClose={() => {
              this.setState({ thing: null })
            }}
            maxWidth="md"
            fullWidth
          >
            {this.state.thing && (
              <React.Fragment>
                <AppBar
                  className={classes.dialogBar}
                  position="static"
                  color="primary"
                  elevation={0}
                >
                  <Container maxWidth={false}>
                    <Toolbar disableGutters>
                      {/*<MdiIcon
                        //className={`${classes.icon} ${classes.icon_textPrimary}`}
                        path={mdiChip}
                        size={1}
                        style={{ fill:'currentColor', marginRight:8 }}
                      />*/}
                      <Typography variant="h6" style={{ flexGrow: 1 }}>
                        Контроллер <ThingIdentity thing={this.state.thing} />
                      </Typography>
                      <IconButton
                        color="inherit"
                        edge="end"
                        onClick={() => {
                          this.setState({ thing: null })
                        }}
                      >
                        <CloseIcon color="inherit" />
                      </IconButton>
                    </Toolbar>
                  </Container>
                </AppBar>
                <DialogContent className={classes.dialogContent}>
                  <Container maxWidth={false}>
                    <ThingDetails thing={this.state.thing} />
                  </Container>
                </DialogContent>
                {/*<DialogActions className={classes.dialogActions}>
                  <Button
                    className={classes.dialogAction}
                    variant="outlined"
                    color="default"
                    onClick={() => {
                      this.setState({ thing: null })
                    }}
                  >
                    Закрыть
                  </Button>
                </DialogActions>*/}
              </React.Fragment>
            )}
          </Dialog>
        </Box>
      </React.Fragment>
    )
  }
}

const styles = theme => ({
  root: {},
  chip: {
    marginLeft: 12,
  },
  dialogContainer: {
    alignItems: 'start',
  },
  dialogPaper: {
    marginTop: 56 + 16,
  },
  dialogBar: {
    backgroundColor: 'rgba(25,25,25,1)',
  },
  dialogContent: {
    padding: theme.spacing(3, 0),
  },
  dialogActions: {
    padding: theme.spacing(1),
    justifyContent: 'flex-start',
  },
  dialogAction: {
    padding: theme.spacing(1, 3),
  },
})

export default withStyles(styles)(Dispatch)
