import { AWS_CONFIG } from '../settings/aws/AwsConfig'
export const ADDRESS_LIST_FETCH = 'ADDRESS_LIST_FETCH'

export function fetchAddressList(user) {
  return async dispatch => {
    dispatch({
      type: ADDRESS_LIST_FETCH,
      status: 'pending',
    })

    const res = await fetch(AWS_CONFIG.restApi + '/addresses')
    const data = await res.json()
    if (res.ok) {
      dispatch({
        type: ADDRESS_LIST_FETCH,
        status: 'success',
        payload: data,
      })
    } else {
      dispatch({
        type: ADDRESS_LIST_FETCH,
        status: 'failure',
        payload: data,
      })
    }
  }
}
