import { AWS_CONFIG } from '../settings/aws/AwsConfig'
export const EVENT_LIST_FETCH = 'EVENTS_LIST_FETCH'

export function fetchEventList(user) {
  return async dispatch => {
    dispatch({
      type: EVENT_LIST_FETCH,
      status: 'pending',
    })

    const res = await fetch(AWS_CONFIG.restApi + '/log')
    const data = await res.json()
    if (res.ok) {
      dispatch({
        type: EVENT_LIST_FETCH,
        status: 'success',
        payload: data,
      })
    } else {
      dispatch({
        type: EVENT_LIST_FETCH,
        status: 'failure',
        payload: data,
      })
    }
  }
}
