import React from 'react'
import { withStyles } from '@material-ui/styles'

import LensIcon from '@material-ui/icons/Lens' // import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import ErrorIcon from '@material-ui/icons/Error'
import NewReleasesIcon from '@material-ui/icons/NewReleases'
import HelpIcon from '@material-ui/icons/Help'

class ThingStatus extends React.PureComponent {
  isDisconnected() {
    const { thing, thingShadow } = this.props
    let date
    if (!thingShadow) {
      date = new Date(Number(`${thing.msgtime}000`))
    } else {
      date = new Date(Number(`${thingShadow.state.reported.msgtime}000`))
    }
    const now = new Date()
    if (now - date > 1000 * 60 * 60 * 2) {
      return true
    }
    return false
  }

  getStatus() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.status
    } else {
      return thingShadow.state.reported.status
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    const disconnected = this.isDisconnected()
    const status = this.getStatus()
    return (
      <React.Fragment>
        {!hideIcon &&
          (status === 'normal' ? (
            <React.Fragment>
              <LensIcon
                className={classes.icon}
                style={{ color: disconnected ? '#3498db' : 'rgb(46,204,113)' }}
              />
            </React.Fragment>
          ) : status === 'almin' ? (
            <React.Fragment>
              <ErrorIcon
                className={classes.icon}
                style={{ color: disconnected ? '#3498db' : 'rgb(252,118,0)' }}
              />
            </React.Fragment>
          ) : status === 'almaj' ? (
            <React.Fragment>
              <NewReleasesIcon
                className={classes.icon}
                style={{ color: disconnected ? '#3498db' : 'rgb(232,15,15)' }}
              />
            </React.Fragment>
          ) : (
            <React.Fragment>
              <HelpIcon className={classes.icon} color="action" />
            </React.Fragment>
          ))}
        {!hideLabel && <span className={classes.label}>Статус</span>}
        {!hideValue &&
          (status === 'normal' ? (
            <React.Fragment>
              {disconnected ? (
                <span>
                  <b>Нет связи</b> (норма)
                </span>
              ) : (
                <span>Норма</span>
              )}
            </React.Fragment>
          ) : status === 'almin' ? (
            <React.Fragment>
              {disconnected ? (
                <span>
                  <b>Нет связи</b> (нарушение)
                </span>
              ) : (
                <span>Нарушение</span>
              )}
            </React.Fragment>
          ) : status === 'almaj' ? (
            <React.Fragment>
              {disconnected ? (
                <span>
                  <b>Нет связи</b> (авария)
                </span>
              ) : (
                <span>Авария</span>
              )}
            </React.Fragment>
          ) : (
            <React.Fragment>
              {disconnected ? (
                <span>
                  <b>Нет связи</b> (неизвестно)
                </span>
              ) : (
                <span>Неизвестно</span>
              )}
            </React.Fragment>
          ))}
      </React.Fragment>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    marginRight: 8,
    verticalAlign: 'middle',
  },
})

export default withStyles(styles)(ThingStatus)
