export function getCsvShka(IotAnalytics, name) {
  return new Promise((resolve, reject) => {
    const params = {
      datasetName: name,
    }
    IotAnalytics.getDatasetContent(params, (err, data) => {
      if (err) {
        reject(err)
      } else {
        resolve(data)
      }
    })
  })
}

export function filterRows(rows, params) {
  // console.warn('filter rows', rows, params)

  return rows.filter(row => {
    let filtered = true
    Object.entries(params).forEach(([k, v]) => {
      if (!filtered) return
      switch (k) {
        case 'address': {
          if (v) {
            filtered = row.group_id === v
            return
          }
        }
        case 'noPower': {
          if (v === true) {
            filtered = false
            if (row.pwr === false || row.pwr === 'false') {
              filtered = true
            }
          }
          return
        }
        case 'noConnection2h': {
          if (v === true) {
            filtered = false
            if (row.msgtime) {
              const ts = new Date(Number(`${row.msgtime}000`))
              const now = new Date()
              if (now - ts >= 1000 * 60 * 60 * 2) {
                filtered = true
              }
            }
          }
          return
        }
        case 'noConnection24h': {
          if (v === true) {
            filtered = false
            if (row.msgtime) {
              const ts = new Date(Number(`${row.msgtime}000`))
              const now = new Date()
              if (now - ts >= 1000 * 60 * 60 * 24) {
                filtered = true
              }
            }
          }
          return
        }
        case 'noEthernet': {
          if (v === true) {
            filtered = false
            if (row.conn !== 'eth') {
              filtered = true
            }
          }
          return
        }
        case 'lowGsm': {
          if (v === true) {
            filtered = false
            if (Number('' + row.rssi || '0') <= -92) {
              filtered = true
            }
          }
          return
        }
        case 'activeEthernet': {
          if (v === true) {
            filtered = false
            if (row.conn === 'eth') {
              filtered = true
            }
          }
          return
        }
        case 'activeGsm': {
          if (v === true) {
            filtered = false
            if (row.conn === 'gsm') {
              filtered = true
            }
          }
          return
        }

        case 'noConnection': {
          if (v === true) {
            filtered = false
            if (row.msgtime) {
              const ts = new Date(Number(`${row.msgtime}000`))
              const now = new Date()
              if (now - ts >= 1000 * 60 * 60 * 2) {
                filtered = true
              }
            }
          }
          return
        }
        case 'statusNormal': {
          if (v === true) {
            filtered = false
            if (row.status === 'normal') {
              if (row.msgtime) {
                const ts = new Date(Number(`${row.msgtime}000`))
                const now = new Date()
                if (now - ts < 1000 * 60 * 60 * 2) {
                  filtered = true
                }
              } else {
                filtered = true
              }
            }
          }
          return
        }
        case 'statusAlmin': {
          if (v === true) {
            filtered = false
            if (row.status === 'almin') {
              if (row.msgtime) {
                const ts = new Date(Number(`${row.msgtime}000`))
                const now = new Date()
                if (now - ts < 1000 * 60 * 60 * 2) {
                  filtered = true
                }
              } else {
                filtered = true
              }
            }
          }
          return
        }
        case 'statusAlmaj': {
          if (v === true) {
            filtered = false
            if (row.status === 'almaj') {
              if (row.msgtime) {
                const ts = new Date(Number(`${row.msgtime}000`))
                const now = new Date()
                if (now - ts < 1000 * 60 * 60 * 2) {
                  filtered = true
                }
              } else {
                filtered = true
              }
            }
          }
          return
        }

        default: {
        }
      }
    })
    return filtered
  })
}
