import { AWS_CONFIG } from '../settings/aws/AwsConfig'

export function checkCredentials(params) {
  return new Promise((resolve, reject) => {
    //console.log(params);
    var AWS = require('aws-sdk')
    var $ = require('jquery')
    var AmazonCognitoIdentity = require('amazon-cognito-identity-js')

    var authenticationData = {
      Username: params.username.toLowerCase(), // your username here
      Password: params.password, // your password here
    }
    var poolData = {
      UserPoolId: AWS_CONFIG.userPoolId,
      ClientId: AWS_CONFIG.clientId,
    }

    console.log(poolData)
    var userPool = new AmazonCognitoIdentity.CognitoUserPool(poolData)
    var userData = {
      Username: authenticationData.Username,
      Pool: userPool,
    }
    var authenticationDetails = new AmazonCognitoIdentity.AuthenticationDetails(
      authenticationData
    )
    var cognitoUser = new AmazonCognitoIdentity.CognitoUser(userData)
    //console.log("blablabla");
    ////////////////////////////////////////////////////////////////////////////////////////
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: function(result) {
        console.log('authenticateUser:onSuccess')
        var idToken = result.getIdToken().getJwtToken()
        var accessToken = result.getAccessToken().getJwtToken()
        var loginCredentialsProvider =
          'cognito-idp.' +
          AWS_CONFIG.region +
          '.amazonaws.com/' +
          AWS_CONFIG.userPoolId
        //console.log(loginCredentialsProvider);
        AWS.config.update({ region: AWS_CONFIG.region })

        // Add the User's Id Token to the Cognito credentials login map.
        AWS.config.credentials = new AWS.CognitoIdentityCredentials({
          IdentityPoolId: AWS_CONFIG.identityPoolId,
          Logins: {
            [loginCredentialsProvider]: idToken,
          },
        })

        AWS.config.credentials.get(error => {
          if (error) {
            console.log('CONFIGERROR')
            AWS.config.credentials.clearCachedId()
            console.error(error)
            reject(error)
          } else {
            //console.log(AWS.config.credentials);
            //AWS.config.region = this.props.user.credentials.region;

            var IotConfig = {
              endpoint: 'iot.' + AWS_CONFIG.region + '.amazonaws.com',
              region: AWS_CONFIG.region,
              accessKeyId: AWS.config.credentials.accessKeyId,
              accessToken: accessToken,
              idToken: idToken,
              secretAccessKey: AWS.config.credentials.secretAccessKey,
              sessionToken: AWS.config.credentials.sessionToken,
            }

            //console.log(IotConfig);
            //Iot = new AWS.Iot(IotConfig);
            //console.log(Iot);
            //console.log("AWS AUTH SUCCESS");

            resolve({
              status: true,
              code: '',
              credentials: IotConfig,
            })
          }
        })

        $('form')[0].reset()
      },

      onFailure: function(err) {
        console.log(err)
        console.log('AWS AUTH ERROR')
        $('.box')
          .addClass('shake')
          .delay(1000)
          .queue(function() {
            $('.box').removeClass('shake')
            $("input[name='password']")
              .focus()
              .select()
            $('.box').dequeue()
          })
        resolve({
          status: false,
          code: 'invalid_login_or_password',
          credentials: {},
        })
      },

      newPasswordRequired: function(userAttributes, requiredAttributes) {
        // User was signed up by an admin and must provide new
        // password and required attributes, if any, to complete
        // authentication.
        console.log('user: ', userAttributes)
        console.log('required: ', requiredAttributes)
        // the api doesn't accept this field back
        delete userAttributes.email_verified
        delete userAttributes.phone_number_verified

        var newPassword = prompt('Input new password', '')

        // store userAttributes on global variable
        userAttributes.name = 'name' // TODO
        userAttributes.family_name = 'surname' // TODO
        var sessionUserAttributes = userAttributes

        console.log(userAttributes)
        cognitoUser.completeNewPasswordChallenge(
          newPassword,
          sessionUserAttributes,
          {
            onSuccess: result => {
              console.log('NEW PASSWORD COMPLETED: ')
              console.log(result)
            },
            onFailure: err => {
              console.log(err)
              reject(err)
            },
          }
        )

        // store userAttributes on global variable
        sessionUserAttributes = userAttributes
      },
    })
  })
}
