import React from 'react'
import PropTypes from 'prop-types'
import { Redirect } from 'react-router-dom'
import Cookies from 'universal-cookie'
const cookies = new Cookies()
export const WRITE_CRED_TO_STORE = 'WRITE_CRED_TO_STORE'

//cookies.set('myCat', 'Pacman', { path: '/' });

class Login extends React.Component {
  state = {
    redirectToPreviousRoute: false,
    username: '',
    password: '',
  }

  componentDidMount() {
    //console.log("cookies.get()=",cookies.get('credentials'));
    var cCred = cookies.get('credentials')
    if (cCred)
      if (
        cCred['name'] &&
        cCred['accessKeyId'] &&
        cCred['endpoint'] &&
        cCred['region'] &&
        cCred['secretAccessKey'] &&
        cCred['accessToken'] &&
        cCred['idToken'] &&
        cCred['sessionToken']
      ) {
        //console.log("AUTH CHECK SUCCESS");
        var params = {
          name: cCred['name'],
          credentials: {
            accessKeyId: cCred['accessKeyId'],
            accessToken: cCred['accessToken'],
            idToken: cCred['idToken'],
            endpoint: cCred['endpoint'],
            region: cCred['region'],
            secretAccessKey: cCred['secretAccessKey'],
            sessionToken: cCred['sessionToken'],
          },
        }
        this.props.writeCred(params, () => {
          this.setState({ redirectToPreviousRoute: true })
        })
      }
  }

  handleSubmit = e => {
    e.preventDefault()
    const { username, password } = this.state

    this.props.logIn(
      {
        username,
        password,
      },
      () => {
        this.setState({ redirectToPreviousRoute: true })
      }
    )
  }

  handleChange = e => {
    const value = e.currentTarget.value
    const fieldName = e.currentTarget.dataset.fieldName

    this.setState(prev => ({
      ...prev,
      [fieldName]: value,
    }))
  }

  render() {
    const { location, errorMsg } = this.props
    const { from } = location.state || { from: { pathname: '/ws' } }
    const { username, password, redirectToPreviousRoute } = this.state

    if (redirectToPreviousRoute) {
      return <Redirect to={from} />
    }

    return (
      <div>
        {errorMsg && <p>{errorMsg}</p>}
        <form className="box" onSubmit={this.handleSubmit}>
          <h1>ST-EYE</h1>
          <input
            data-field-name={'username'}
            type={'text'}
            onChange={this.handleChange}
            placeholder={'Username'}
            value={username}
          />
          <input
            data-field-name={'password'}
            type={'password'}
            onChange={this.handleChange}
            placeholder={'Password'}
            value={password}
          />
          <button type="submit">Login</button>
        </form>
      </div>
    )
  }
}

Login.propTypes = {
  logIn: PropTypes.func.isRequired,
  credentials: PropTypes.object,
  errorMsg: PropTypes.string,
}

export default Login
