import React from 'react'
import { connect } from 'react-redux'
import Analyst from '../components/Analyst'
import { fetchAnalystData } from '../actions/AnalystActions'
import AnalystGroupListContainer from './AnalystGroupListContainer'
class AnalystContainer extends React.Component {
  render() {
    return (
      <Analyst groupListComponent={AnalystGroupListContainer} {...this.props} />
    )
  }
}

const mapStateToProps = state => ({})

const mapDispatchToProps = dispatch => ({
  onFetchData: params => dispatch(fetchAnalystData(params)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AnalystContainer)
