import React from 'react'
import PropTypes from 'prop-types'

import { withStyles } from '@material-ui/styles'
import { withRouter } from 'react-router'

import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import FormGroup from '@material-ui/core/FormGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Checkbox from '@material-ui/core/Checkbox'
import NativeSelect from '@material-ui/core/NativeSelect'

import { parse, stringify } from 'query-string'
import Box from '@material-ui/core/Box'

const PARAMS = {
  address: null,
  noPower: false,
  noConnection2h: false,
  noConnection24h: false,
  noEthernet: false,
  lowGsm: false,
  activeEthernet: false,
  activeGsm: false,
  noConnection: false,
  statusNormal: false,
  statusAlmin: false,
  statusAlmaj: false,
}

class Filter extends React.PureComponent {
  state = {
    params: {
      ...PARAMS,
    },
  }

  componentDidMount() {
    const {
      location: { search },
      setParams,
    } = this.props
    const searchParams = parse(search)
    const params = Object.keys(PARAMS).reduce((params, k) => {
      switch (k) {
        case 'address': {
          params[k] = searchParams[k] || null
          break
        }
        default: {
          params[k] = !!searchParams[k]
        }
      }
      return searchParams
    }, {})
    setParams(params)
    const { user, fetchGroupsList } = this.props
    fetchGroupsList(user)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.params !== this.props.params) {
      const { params } = this.props
      this.setState({ params })
    }
  }

  handleChangeAddress = ({ target: { value: address } }) => {
    this.setState({
      params: {
        ...this.state.params,
        address,
      },
    })
  }

  handleChangeCheckbox = ({ target: { value, checked } }) => {
    this.setState({
      params: {
        ...this.state.params,
        [value]: checked,
      },
    })
  }

  handleSubmit = () => {
    let hasParams = false
    const searchParamsOrder = []
    const searchParams = Object.entries(this.state.params).reduce(
      (searchParams, [k, v]) => {
        if (!hasParams && v) {
          hasParams = true
        }
        searchParamsOrder.push(k)
        searchParams[k] = v || undefined
        return searchParams
      },
      {}
    )
    const { history, location } = this.props
    if (!hasParams) {
      history.push(location.pathname)
    } else {
      const search = stringify(searchParams, {
        sort: (a, b) =>
          searchParamsOrder.indexOf(a) - searchParamsOrder.indexOf(b),
      })
      history.push(`${location.pathname}?${search}`)
    }
    this.props.setParams(this.state.params)
  }

  isSubmitted() {
    return (
      JSON.stringify(this.state.params) === JSON.stringify(this.props.params)
    )
  }

  render() {
    const { classes, groupsList } = this.props
    return (
      <div className={classes.root}>
        <Box
          position="absolute"
          style={{ top: 0, right: 0, bottom: -1, left: 0, overflowY: 'auto' }}
          p={2}
        >
          <Typography className={classes.heading} variant="h6" paragraph>
            Фильтр объектов
          </Typography>

          <Typography
            className={classes.subtitle}
            variant="subtitle1"
            color="primary"
          >
            Адрес
          </Typography>
          <NativeSelect
            className={classes.address}
            classes={{ select: classes.addressSelect }}
            value={this.state.params.address || ''}
            onChange={this.handleChangeAddress}
            fullWidth
          >
            <option value={''} style={{ color: '#aaa' }}>
              Выберите адрес
            </option>
            {!!groupsList &&
              !!groupsList.Items &&
              groupsList.Items.map(item => (
                <option key={item.id.N} value={item.id.N}>
                  {item.name.S}
                </option>
              ))}
          </NativeSelect>

          <Typography
            className={classes.subtitle}
            variant="subtitle1"
            color="primary"
          >
            Состояние
          </Typography>
          <FormGroup>
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Норма"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="statusNormal"
                  checked={this.state.params.statusNormal}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Нарушение"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="statusAlmin"
                  checked={this.state.params.statusAlmin}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Авария"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="statusAlmaj"
                  checked={this.state.params.statusAlmaj}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Нет связи"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="noConnection"
                  checked={this.state.params.noConnection}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
          </FormGroup>

          <Typography
            className={classes.subtitle}
            variant="subtitle1"
            color="primary"
          >
            Связь
          </Typography>
          <FormGroup>
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Активный канал Ethernet"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="activeEthernet"
                  checked={this.state.params.activeEthernet}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Активный канал GSM"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="activeGsm"
                  checked={this.state.params.activeGsm}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Нет связи более 2 ч."
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="noConnection2h"
                  checked={this.state.params.noConnection2h}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Нет связи более 24 ч."
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="noConnection24h"
                  checked={this.state.params.noConnection24h}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Отключен Ethernet"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="noEthernet"
                  checked={this.state.params.noEthernet}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Низкий уровень приема GSM"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="lowGsm"
                  checked={this.state.params.lowGsm}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
          </FormGroup>

          <Typography
            className={classes.subtitle}
            variant="subtitle1"
            color="primary"
          >
            Другое
          </Typography>
          <FormGroup>
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Отключено питание"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  value="noPower"
                  checked={this.state.params.noPower}
                  onChange={this.handleChangeCheckbox}
                />
              }
            />
            <FormControlLabel
              classes={{
                root: classes.checkboxControl,
                label: classes.checkboxControlLabel,
              }}
              label="Аномалии в данных"
              control={
                <Checkbox
                  classes={{ root: classes.checkbox }}
                  color="primary"
                  //checked={this.state.params.noPower}
                  //onChange={this.handleChangeCheckbox}
                />
              }
            />
          </FormGroup>

          <Button
            className={classes.submit}
            variant={this.isSubmitted() ? 'outlined' : 'contained'}
            color="secondary"
            onClick={this.handleSubmit}
            fullWidth
          >
            Применить
          </Button>
        </Box>
      </div>
    )
  }
}

Filter.propTypes = {
  params: PropTypes.object.isRequired,
}

const styles = theme => ({
  root: {
    position: 'relative',
    width: '15%',
    minWidth: 240,
    margin: theme.spacing(2),
    //padding: theme.spacing(2),
    color: '#fff',
    background: 'rgba(25,25,25,1)',
    borderRadius: theme.shape.borderRadius,
    boxShadow: '1px 1px 2px rgba(25,25,25,.16)',
  },
  heading: {},
  subtitle: {
    marginTop: theme.spacing(2),
    fontWeight: 700,
  },
  address: {
    marginTop: theme.spacing(1.5),
    marginBottom: theme.spacing(1),
    backgroundColor: theme.palette.background.paper,
    borderRadius: theme.shape.borderRadius,
    '&::before': {
      borderBottomColor: 'transparent !important',
    },
  },
  addressSelect: {
    padding: theme.spacing(1, 3, 1, 1.5),
  },
  checkboxControl: {
    display: 'flex',
    alignItems: 'center',
  },
  checkboxControlLabel: {
    ...theme.typography.body2,
    fontSize: '12px',
    paddingTop: 2,
  },
  checkbox: {
    color: theme.palette.primary.main,
  },
  submit: {
    marginTop: theme.spacing(2.5),
  },
})

export default withRouter(withStyles(styles)(Filter))
