import {
  LOG_IN,
  LOG_OUT,
  LOG_IN_FAILURE,
  WRITE_CRED_TO_STORE,
} from '../actions/SessionActions'
import Cookies from 'universal-cookie'
const cookies = new Cookies()

const initialState = {
  user: null,
  errorMsg: '',
}

export default (state = initialState, action) => {
  switch (action.type) {
    case LOG_IN: {
      cookies.remove('credentials')
      cookies.set(
        'credentials',
        {
          ...action.payload.credentials,
          name: action.payload.name,
        },
        {
          path: '/',
          maxAge: 60 * 10,
        }
      )
      return {
        ...state,
        user: {
          name: action.payload.name,
          credentials: action.payload.credentials,
        },
        errorMsg: '',
      }
    }
    case LOG_OUT: {
      cookies.remove('credentials')
      return {
        ...state,
        user: null,
        errorMsg: '',
      }
    }

    case WRITE_CRED_TO_STORE:
      return {
        ...state,
        user: {
          name: action.payload.name,
          credentials: action.payload.credentials,
        },
      }

    case LOG_IN_FAILURE:
      return {
        ...state,
        errorMsg: action.payload.errorMsg,
      }
    default:
      return state
  }
}
