import React from 'react'
import { withStyles } from '@material-ui/styles'

import { FixedSizeList as List } from 'react-window'
import AutoSizer from 'react-virtualized/dist/es/AutoSizer'
import DispatchThingListItem from './DispatchThingListItem'
import { filterRows } from '../services/CsvShkaService'
import Box from '@material-ui/core/Box'

class DispatchThingList extends React.PureComponent {
  state = {
    data: null,
  }

  componentDidMount() {
    const { onFetchThingList } = this.props
    onFetchThingList()
    const { onFetchAddressList } = this.props
    onFetchAddressList()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { thingListParams, thingList } = this.props
    if (
      thingList &&
      (thingListParams !== prevProps.thingListParams ||
        thingList !== prevProps.thingList)
    ) {
      const data = filterRows(thingList, thingListParams)
      this.setState({
        data,
      })
    }
  }

  render() {
    const { classes, addressList, onClickThing } = this.props
    const { data } = this.state
    if (!data || !addressList || !addressList.Items) {
      return null
    }
    const addressById = addressList.Items.reduce((addressById, item) => {
      addressById[item.group_id.N] = item
      return addressById
    }, {})
    return (
      <Box flexGrow={1} display="flex" flexDirection="column">
        <Box>
          <div className={`${classes.head}`}>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            >
              Статус
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_object}`}
            >
              Объект
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_problems}`}
            >
              Проблемные ситуации
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_details}`}
            >
              Состояние
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_updated}`}
            >
              Обновлено
            </div>
          </div>
        </Box>
        <Box flexGrow={1} position="relative">
          <AutoSizer>
            {({ width, height }) => (
              <List
                className={classes.list}
                width={width}
                height={height}
                itemCount={data.length}
                itemSize={48}
                overscanCount={1}
                itemData={{
                  classes,
                  data,
                  addressById,
                  onClickThing,
                }}
              >
                {DispatchThingListItem}
              </List>
            )}
          </AutoSizer>
        </Box>
      </Box>
    )
  }
}

const styles = theme => ({
  root: {},
  head: {
    display: 'flex',
    flexWrap: 'nowrap',
    alignItems: 'start',
    WebkitBoxOrient: 'horizontal',
    WebkitBoxDirection: 'normal',
    WebkitBoxAlign: 'start',
    marginTop: -12,
    '& $itemColumn': {
      height: 24,
      whiteSpace: 'nowrap',
    },
  },
  item: {
    display: 'flex',
    flexWrap: 'nowrap',
    alignItems: 'start',
    WebkitBoxOrient: 'horizontal',
    WebkitBoxDirection: 'normal',
    WebkitBoxAlign: 'start',
    boxShadow: `inset 0 1px 0 0 rgba(0, 0, 0, 0.12)`,
    '&:last-child': {
      boxShadow: `inset 0 1px 0 0 rgba(0, 0, 0, 0.12), 0 1px 0 0 rgba(0, 0, 0, 0.12)`,
    },
  },
  item_noConnection: {
    '& $itemColumn_details': {
      opacity: 0.37,
    },
  },
  itemColumn: {
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'content-box',
    minWidth: 0,
    overflow: 'hidden',
    //alignItems: 'center',
    height: 48,
    paddingLeft: 12,
    paddingRight: 12,
    lineHeight: '16px',
    fontSize: '14px',
    '&:first-child': {
      paddingLeft: theme.spacing(3),
    },
  },
  itemColumn_status: {
    flex: `0 0 ${theme.spacing(3) + 120 + theme.spacing(3)}px`,
    whiteSpace: 'nowrap',
  },
  itemColumn_object: {
    flex: '0 1 30%',
  },
  itemColumn_problems: {
    flex: '0 1 20%',
    '& strong': {
      '&:not(:first-child)': {
        textTransform: 'lowercase',
      },
      '&:not(:last-child)::after': {
        content: '","',
        marginRight: 4,
      },
    },
  },
  itemColumn_details: {
    flex: '1 1 20%',
    minWidth: 160,
  },
  itemColumn_updated: {
    flex: `0 0 ${theme.spacing(3) + 60 + theme.spacing(3)}px`,
    whiteSpace: 'nowrap',
  },
})

export default withStyles(styles)(DispatchThingList)
