import React from 'react'
import { connect } from 'react-redux'
import WsSelect from '../components/WsSelect'
import Loading from '../components/Loading'
import { AWS_CONFIG } from '../settings/aws/AwsConfig'
var AWS = require('aws-sdk')

class WsSelectContainer extends React.Component {
  state = {
    groups: null,
  }

  getUserGroups() {
    return new Promise((resolve, reject) => {
      var listGroupsParams = {
        Username: this.props.user.name,
        UserPoolId: AWS_CONFIG.userPoolId,
      }

      var loginCredentialsProvider =
        'cognito-idp.' +
        AWS_CONFIG.region +
        '.amazonaws.com/' +
        AWS_CONFIG.userPoolId
      AWS.config.update({ region: AWS_CONFIG.region })

      // Add the User's Id Token to the Cognito credentials login map.
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: AWS_CONFIG.identityPoolId,
        Logins: {
          [loginCredentialsProvider]: this.props.user.credentials.idToken,
        },
      })

      AWS.config.credentials.get(error => {
        if (error) {
          console.log('CONFIGERROR')
          AWS.config.credentials.clearCachedId()
          console.error(error)
          reject(error)
        } else {
          var cognitoISP = new AWS.CognitoIdentityServiceProvider()
          cognitoISP.adminListGroupsForUser(listGroupsParams, function(
            err,
            data
          ) {
            if (err) {
              console.log(err, err.stack)
              reject(err)
            } // an error occurred
            resolve(data)
          })
        }
      })
    })
  }

  componentDidMount() {
    //console.log(this.props.user.credentials.accessKeyId);
    //console.log('deez nuts',this.props);
    this._asyncRequest = this.getUserGroups().then(data => {
      this._asyncRequest = null
      //this.setState({groups: groups});
      //console.log(data.Groups);
      this.setState({
        groups: data.Groups.map(el => {
          return el.GroupName
        }),
      })
      //console.log("stategroups", this.state.groups);

      //console.log('getUserGroups promise resolved');
    })
    //setTimeout(this.setState({groups: "group1"}), 10000)
  }

  render() {
    console.log('WsSelectContainer state:', this.state)
    console.log('WsSelectContainer props:', this.props)
    if (!this.state.groups) return <Loading />
    return <WsSelect user={{ ...this.props.user, groups: this.state.groups }} />
  }
}

const mapStateToProps = state => ({
  user: state.session.user,
})

export default connect(mapStateToProps)(WsSelectContainer)
