import React from 'react'
import { connect } from 'react-redux'
import { fetchEventList } from '../actions/EventListActions'

import DispatchEventList from '../components/DispatchEventList'

const mapStateToProps = state => ({
  user: state.session.user,
  list: state.eventList.list,
  listPending: state.eventList.listPending,
  listError: state.eventList.listError,
})

const mapDispatchToProps = dispatch => ({
  fetchEventList: params => dispatch(fetchEventList(params)),
})

class DispatchEventListContainer extends React.PureComponent {
  componentDidMount() {
    this.props.fetchEventList()
  }

  render() {
    return <DispatchEventList {...this.props} />
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(
  DispatchEventListContainer
)
