import React from 'react'
import { withStyles } from '@material-ui/styles'

import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'

class Analyst extends React.PureComponent {
  componentDidMount() {
    const { onFetchData } = this.props
    onFetchData()
  }

  render() {
    const { classes } = this.props
    const { groupListComponent: GroupList } = this.props
    return (
      <Box className={classes.root} display="flex" flexGrow={1}>
        <Paper
          component={Box}
          flexGrow={1}
          display="flex"
          flexDirection="column"
          m={2}
        >
          <Toolbar>
            <Typography variant="h6">Текущие показания</Typography>
          </Toolbar>
          <GroupList />
        </Paper>
      </Box>
    )
  }
}

const styles = theme => ({
  root: {},
})

export default withStyles(styles)(Analyst)
