import React from 'react'
import { Link } from 'react-router-dom'

const User = ({ user }) => {
  //console.log(user);
  if (user)
    if (user.name)
      return (
        <React.Fragment>
          <div className="username">{user.name}</div>
        </React.Fragment>
      )
    else
      return (
        <React.Fragment>
          <Link className="loginLink" to="/login">
            <div className="username">{'Войти'}</div>
          </Link>
        </React.Fragment>
      )
}

export default User
