export function initAwsMqttClient() {
  console.warn('init', AWS.config.region, AWS.config.credentials)
  const requestUrl = SigV4getSignedUrl(
    'a2xnjhywaij79l-ats.iot.us-west-2.amazonaws.com',
    AWS.config.region,
    AWS.config.credentials
  )
  const client = new Paho.Client(requestUrl, AWS_CONFIG.clientId)
  client.onMessageArrived = message => {
    console.log('mqtt message', message.destinationName)
    if (message.destinationName === '$aws/things/Test7/shadow/get/accepted') {
      console.log('RESOLVE SHADOW', JSON.parse(message.payloadString))
    }
  }
  const connectOptions = {
    onSuccess: function() {
      // connect succeeded
      console.log('connected')
      client.subscribe('$aws/things/Test7/shadow/get/accepted')
      client.subscribe('$aws/things/Test7/shadow/get/rejected')
      console.log('subscribed', Paho)
      setTimeout(() => {
        const message = new Paho.Message('')
        message.destinationName = '$aws/things/Test7/shadow/get'
        client.send(message)
      }, 1000)
      // message = new Paho.MQTT.Message("Hello");
      // message.destinationName = "World";
      // client.send(message);
    },
    useSSL: true,
    //timeout: 3,
    mqttVersion: 4,
    onFailure: function(err) {
      // connect failed
      console.log('error', err)
    },
  }
  client.connect(connectOptions)
}
