import { checkCredentials } from '../helpers/session'

export const LOG_IN = 'LOG_IN'
export const LOG_OUT = 'LOG_OUT'
export const LOG_IN_FAILURE = 'LOG_IN_FAILURE'
export const WRITE_CRED_TO_STORE = 'WRITE_CRED_TO_STORE'

export function writeCred(params, cb) {
  return dispatch => {
    dispatch({
      type: WRITE_CRED_TO_STORE,
      payload: {
        name: params.name,
        credentials: params.credentials,
      },
    })
    cb()
  }
}

export function logIn(params, cb) {
  return dispatch => {
    checkCredentials(params)
      .then(response => {
        //console.log('promise loaded')
        if (response.status) {
          dispatch({
            type: LOG_IN,
            payload: {
              name: params.username,
              credentials: response.credentials,
            },
          })
          cb()
        } else {
          dispatch({
            type: LOG_IN_FAILURE,
            payload: {
              errorMsg: response.code,
            },
            error: true, // https://github.com/redux-utilities/flux-standard-action
          })
        }
      })
      .catch(error => {
        console.log('Promise error:', error)
        dispatch({
          type: LOG_IN_FAILURE,
          payload: {
            errorMsg: error,
          },
          error: true, // https://github.com/redux-utilities/flux-standard-action
        })
      })
  }
}

export function logOut() {
  return {
    type: LOG_OUT,
  }
}
