import { AWS_CONFIG } from '../settings/aws/AwsConfig'
export const GROUPS_LIST_FETCH = 'GROUPS_LIST_FETCH'

export function fetchGroupsList(user) {
  return async dispatch => {
    dispatch({
      type: GROUPS_LIST_FETCH,
      status: 'pending',
    })

    const res = await fetch(AWS_CONFIG.restApi + '/groups')
    const data = await res.json()
    if (res.ok) {
      dispatch({
        type: GROUPS_LIST_FETCH,
        status: 'success',
        payload: data,
      })
    } else {
      dispatch({
        type: GROUPS_LIST_FETCH,
        status: 'failure',
        payload: data,
      })
    }
  }
}
