import React from 'react'
import { withStyles } from '@material-ui/styles'

import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Switch from '@material-ui/core/Switch'
import Box from '@material-ui/core/Box'
import CircularProgress from '@material-ui/core/CircularProgress'

class ThingRelays extends React.PureComponent {
  isThingRelayDesired(i) {
    const { thingShadow: { state: { desired, reported } } } = this.props
    if (desired) {
      const desiredRelay = desired.relay
      const reportedRelay = reported.relay
      if (desiredRelay && desiredRelay[i] !== reportedRelay[i]) {
        return true
      }
    }
    return false
  }

  isThingRelayOn(i) {
    const { thingShadow, unsavedRelayState } = this.props
    if (unsavedRelayState !== undefined) {
      return !!unsavedRelayState[i]
    } else {
      if (thingShadow.state.desired && thingShadow.state.desired.relay) {
        return !!thingShadow.state.desired.relay[i]
      } else if (
        thingShadow.state.reported &&
        thingShadow.state.reported.relay
      ) {
        return !!thingShadow.state.reported.relay[i]
      }
    }
  }

  render() {
    const { classes, thing, thingShadow, onChangeRelay } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <div className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Реле</TableCell>
              <TableCell>Показания</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {!!thingShadow &&
              !!thingShadow.state.reported.relay &&
              thingShadow.state.reported.relay.map((r, i) => (
                <TableRow key={i}>
                  <TableCell component="th" scope="row">
                    Реле {i}
                  </TableCell>
                  <TableCell>
                    {this.isThingRelayOn(i) ? 'включено' : 'выключено'}
                  </TableCell>
                  <TableCell>
                    <Box display="inline-flex" alignItems="center">
                      {this.isThingRelayDesired(i) && (
                        <CircularProgress style={{ width: 20, height: 20 }} />
                      )}
                      <Switch
                        color={
                          this.isThingRelayDesired(i) ? 'primary' : 'secondary'
                        }
                        disabled={!thingShadow}
                        checked={this.isThingRelayOn(i)}
                        onChange={({ target: { checked: on } }) => {
                          onChangeRelay(i, on)
                        }}
                      />
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </div>
    )
  }
}

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  table: {
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(3),
    '& thead th': {
      padding: theme.spacing(0, 2),
      lineHeight: '24px',
      '&:first-child': {
        paddingLeft: 0,
      },
    },
    '& tbody th, & tbody td': {
      height: 48,
      padding: theme.spacing(0, 2),
      lineHeight: '24px',
      '&:first-child': {
        paddingLeft: 0,
      },
    },
    '& tbody th': {
      width: '37%',
    },
    '& tbody td:last-child': {
      width: 1,
      paddingRight: 0,
      textAlign: 'right',
    },
    '& > tbody > tr:last-child th, & > tbody > tr:last-child td': {
      borderBottom: 0,
    },
  },
})

export default withStyles(styles)(ThingRelays)
