import React from 'react'
import { Switch, Route } from 'react-router-dom'
import PrivateRoute from './containers/PrivateRoute'
import LoginContainer from './containers/LoginContainer'
import LinkBtn from './components/LinkBtn'
import UserContainer from './containers/UserContainer'
import AnalystContainer from './containers/AnalystContainer'
import WsSelectContainer from './containers/WsSelectContainer'
import DispatchContainer from './containers/DispatchContainer'
import NotFound from './components/NotFound'
import './App.css'

const DEBUG = true

function DebugBtns(props) {
  if (props.debug) {
    return (
      <React.Fragment>
        <LinkBtn to="/ws" label={'Выбор ARM'} />
      </React.Fragment>
    )
  }
  return null
}

const App = ({ store }) => (
  <div className="rootDiv">
    <header className="header">
      <div className="logo">
        <span className="appName">ST-Eye</span>
        <span className="separator"></span>
        <span className="pageName"></span>
      </div>
      <div className="top-menu">
        <DebugBtns debug={DEBUG} />
      </div>
      <UserContainer />
    </header>
    <Switch>
      <Route exact path="/" component={LoginContainer} />
      <Route path="/login" component={LoginContainer} />
      <PrivateRoute path="/analyst" component={AnalystContainer} />
      <PrivateRoute path="/dispatch" component={DispatchContainer} />
      <PrivateRoute path="/ws" component={WsSelectContainer} />
      <Route component={NotFound} />
    </Switch>
  </div>
)

export default App
