import { THING_LIST_FETCH } from '../actions/ThingListActions'

const initialState = {
  data: null,
  dataPending: false,
  dataError: null,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case THING_LIST_FETCH: {
      const { status, payload } = action
      return {
        ...state,
        data: status === 'success' ? payload : null,
        dataPending: status === 'pending',
        dataError: status === 'failure' ? payload : null,
      }
    }

    default: {
      return state
    }
  }
}
