import React from 'react'
import { withStyles } from '@material-ui/styles'

import { FixedSizeList as List } from 'react-window'
import AutoSizer from 'react-virtualized/dist/es/AutoSizer'
import DispatchEventListItem from './DispatchEventListItem'

class DispatchEventList extends React.PureComponent {
  state = {}

  render() {
    const { classes, user, list, onClickThing } = this.props
    if (!list) {
      return null
    }
    return (
      <AutoSizer>
        {({ width, height }) => (
          <List
            className={classes.list}
            width={width}
            height={height}
            itemCount={list.Items.length}
            itemSize={48}
            overscanCount={1}
            itemData={{
              classes,
              list,
              onClickThing,
            }}
          >
            {DispatchEventListItem}
          </List>
        )}
      </AutoSizer>
    )
  }
}

const styles = theme => ({
  root: {},
  item: {
    display: 'flex',
    flexWrap: 'nowrap',
    alignItems: 'start',
    WebkitBoxOrient: 'horizontal',
    WebkitBoxDirection: 'normal',
    WebkitBoxAlign: 'start',
    boxShadow: `inset 0 1px 0 0 rgba(0, 0, 0, 0.12)`,
    '&:last-child': {
      boxShadow: `inset 0 1px 0 0 rgba(0, 0, 0, 0.12), 0 1px 0 0 rgba(0, 0, 0, 0.12)`,
    },
  },
  itemColumn: {
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'border-box',
    minWidth: 0,
    overflow: 'hidden',
    //alignItems: 'center',
    height: 48,
    paddingLeft: 12,
    paddingRight: 12,
    lineHeight: '16px',
    fontSize: '14px',
    '&:first-child': {
      paddingLeft: theme.spacing(3),
    },
  },
  itemColumn_level: {
    flex: `0 0 ${theme.spacing(3) + 24 + theme.spacing(3)}px`,
    whiteSpace: 'nowrap',
  },
  itemColumn_device: {
    flex: '0 1 20%',
  },
  itemColumn_message: {
    flex: '1 1 40%',
  },
  itemColumn_time: {},
  itemIcon: {
    width: 16,
    height: 16,
    marginLeft: 4,
  },
})

export default withStyles(styles)(DispatchEventList)
