import React from 'react'
import PropTypes from 'prop-types'
import Loading from '../components/Loading'
import LinkBtn from '../components/LinkBtn'
import { NavLink } from 'react-router-dom'

const WsSelect = ({ user }) => {
  //console.log(user)
  var listButtons
  if (user.groups) {
    var i = 0
    listButtons = user.groups.map(group => (
      <LinkBtn key={i++} to={group} label={group} />
    ))
  }
  return (
    <div className={'news-list'}>
      {!user.groups ? (
        <Loading />
      ) : (
        <React.Fragment>
          <div className="selection_header">
            <h1>Выберите автоматизированное рабочее место</h1>
          </div>
          <div className="selection_container">
            <div className="wspanel">
              <NavLink to="/analyst">
                <div>
                  <img
                    src={require('../resources/analytics.svg')}
                    alt="analytics_img"
                  ></img>
                </div>
                <hr />
                <div className="wspanel_title">
                  <h3>Руководитель</h3>
                  <span className="stitle">.</span>
                </div>
              </NavLink>
            </div>
            <div className="wspanel">
              <NavLink to="/dispatch">
                <div>
                  <img
                    src={require('../resources/dispatching.svg')}
                    alt="dispatching_img"
                  ></img>
                </div>
                <hr />
                <div className="wspanel_title">
                  <h3>Диспетчер</h3>
                  <span className="stitle">.</span>
                </div>
              </NavLink>
            </div>
          </div>
        </React.Fragment>
      )}
    </div>
  )
}

WsSelect.propTypes = {
  user: PropTypes.object.isRequired,
}

export default WsSelect
