import React from 'react'
import { withStyles } from '@material-ui/styles'

import NotificationsActiveIcon from '@material-ui/icons/NotificationsActive'
import NotificationsIcon from '@material-ui/icons/NotificationsOutlined'
import Tooltip from '@material-ui/core/Tooltip'

class ThingAlarm extends React.PureComponent {
  isActive() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.alarm === true || thing.alarm === 'true'
    } else {
      return thingShadow.state.reported.alarm === true
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
      whenActive = null,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon &&
          (hideLabel || hideValue ? (
            <Tooltip
              title={
                <span className={classes.tooltip}>
                  Сигнализация: <span>{this.renderValue()}</span>
                </span>
              }
            >
              {this.renderIcon()}
            </Tooltip>
          ) : (
            this.renderIcon()
          ))}
        {!hideLabel && <span className={classes.label}>Сигнализация</span>}
        {!hideValue && this.renderValue()}
        {whenActive && this.isActive() && whenActive}
      </span>
    )
  }

  renderIcon() {
    const { classes } = this.props
    return this.isActive() ? (
      <NotificationsActiveIcon
        className={`${classes.icon} ${classes.icon_textPrimary}`}
      />
    ) : (
      <NotificationsIcon
        className={`${classes.icon} ${classes.icon_textSecondary}`}
      />
    )
  }

  renderValue() {
    const { classes } = this.props
    return this.isActive() ? (
      <strong className={classes.value}>Активна</strong>
    ) : (
      <span className={classes.value}>Не активна</span>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
  tooltip: {
    fontWeight: 'normal',
    '& > span': {
      textTransform: 'lowercase',
    },
  },
})

export default withStyles(styles)(ThingAlarm)
