import React from 'react'
import { withStyles } from '@material-ui/styles'
import LensIcon from '@material-ui/icons/Lens' // import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import ErrorIcon from '@material-ui/icons/Error'
import NewReleasesIcon from '@material-ui/icons/NewReleases'
import HelpIcon from '@material-ui/icons/Help'

class SensorStatus extends React.PureComponent {
  state = {}

  render() {
    const { classes, sensor } = this.props
    if (!sensor) {
      return null
    }
    return (
      <React.Fragment>
        {sensor.status === 'normal' ? (
          <React.Fragment>
            <LensIcon
              className={classes.icon}
              style={{ color: 'rgb(46,204,113)' }}
            />
            <span>Норма</span>
          </React.Fragment>
        ) : sensor.status === 'almin' ? (
          <React.Fragment>
            <ErrorIcon
              className={classes.icon}
              style={{ color: 'rgb(252,118,0)' }}
            />
            <span>Нарушение</span>
          </React.Fragment>
        ) : sensor.status === 'almaj' ? (
          <React.Fragment>
            <NewReleasesIcon
              className={classes.icon}
              style={{ color: 'rgb(232,15,15)' }}
            />
            <span>Авария</span>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <HelpIcon className={classes.icon} color="action" />
            <span>Неизвестно</span>
          </React.Fragment>
        )}
      </React.Fragment>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    width: 16,
    height: 16,
    marginLeft: 4,
    marginRight: 8,
    verticalAlign: 'middle',
  },
})

export default withStyles(styles)(SensorStatus)
