import React from 'react'
import { withStyles } from '@material-ui/styles'

import { FixedSizeList as List } from 'react-window'
import AutoSizer from 'react-virtualized/dist/es/AutoSizer'
import AnalystGroupListItem from './AnalystGroupListItem'
import Box from '@material-ui/core/Box'

class AnalystGroupList extends React.PureComponent {
  render() {
    const { classes, total, groups } = this.props
    if (!groups) {
      return null
    }
    return (
      <Box flexGrow={1} display="flex" flexDirection="column">
        <Box>
          <div className={`${classes.head}`}>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_title}`}
            ></div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_count}`}
            >
              Всего
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_error}`}
            >
              % с неполадками
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_service}`}
            >
              Сервис
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            >
              Нет связи
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            >
              Норма
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            >
              Нарушение
            </div>
            <div
              className={`${classes.itemColumn} ${classes.itemColumn_status}`}
            >
              Авария
            </div>
          </div>
        </Box>
        <Box flexGrow={1} position="relative">
          <AutoSizer>
            {({ width, height }) => (
              <List
                className={classes.list}
                width={width}
                height={height}
                itemCount={groups.length + 1}
                itemSize={49}
                overscanCount={5}
                itemData={{
                  classes,
                  total,
                  groups,
                }}
              >
                {AnalystGroupListItem}
              </List>
            )}
          </AutoSizer>
        </Box>
      </Box>
    )
  }
}

const styles = theme => ({
  root: {},
  head: {
    display: 'flex',
    flexWrap: 'nowrap',
    alignItems: 'start',
    WebkitBoxOrient: 'horizontal',
    WebkitBoxDirection: 'normal',
    WebkitBoxAlign: 'start',
  },
  item: {
    display: 'flex',
    flexWrap: 'nowrap',
    alignItems: 'start',
    WebkitBoxOrient: 'horizontal',
    WebkitBoxDirection: 'normal',
    WebkitBoxAlign: 'start',
    boxShadow: `inset 0 1px 0 0 rgba(0, 0, 0, 0.12)`,
    '&:last-child': {
      boxShadow: `inset 0 1px 0 0 rgba(0, 0, 0, 0.12), 0 1px 0 0 rgba(0, 0, 0, 0.12)`,
    },
  },
  itemColumn: {
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'border-box',
    minWidth: 0,
    overflow: 'hidden',
    //alignItems: 'center',
    height: 48,
    paddingLeft: 12,
    paddingRight: 12,
    lineHeight: '16px',
    fontSize: '14px',
    '&:first-child': {
      paddingLeft: theme.spacing(3),
    },
    '&:last-child': {
      paddingRight: theme.spacing(3),
    },
  },
  itemColumn_title: {
    flex: '2 2 25%',
  },
  itemColumn_count: {
    flex: `1 1 10%`,
    whiteSpace: 'nowrap',
    justifyContent: 'flex-end',
    '$item &': {
      textDecoration: 'none',
      color: theme.palette.secondary.main,
    },
    '$item &:hover': {
      textDecoration: 'underline',
      color: theme.palette.primary.main,
    },
  },
  itemColumn_error: {
    flex: `1 1 15%`,
    whiteSpace: 'nowrap',
    justifyContent: 'flex-end',
  },
  itemColumn_service: {
    flex: `1 1 10%`,
    whiteSpace: 'nowrap',
    justifyContent: 'flex-end',
  },
  itemColumn_status: {
    flex: `2 2 10%`,
    whiteSpace: 'nowrap',
    justifyContent: 'flex-end',
    '$item &': {
      textDecoration: 'none',
      color: theme.palette.secondary.main,
    },
    '$item &:hover': {
      textDecoration: 'underline',
      color: theme.palette.primary.main,
    },
  },
})

export default withStyles(styles)(AnalystGroupList)
