import { AWS_CONFIG } from '../settings/aws/AwsConfig'
import aws4 from 'aws4'
import * as AWS from 'aws-sdk'
import * as getCSV from 'get-csv'
import { getCsvShka } from '../services/CsvShkaService'

export const DATA_FETCH = 'ANALYST_DATA_FETCH'

export function fetchAnalystData() {
  return async (dispatch, getState) => {
    dispatch({
      type: DATA_FETCH,
      status: 'pending',
    })
    try {
      const {
        session: { user },
      } = getState()
      AWS.config.update({ region: AWS_CONFIG.region })

      const opts = {
        host: 'iot.us-west-2.amazonaws.com',
        method: 'GET',
        headers: {
          auth: user.credentials.idToken,
        },
      }
      aws4.sign(opts, {
        accessKeyId: user.credentials.accessKeyId,
        secretAccessKey: user.credentials.secretAccessKey,
        sessionToken: user.credentials.sessionToken,
      })
      const res = await fetch(AWS_CONFIG.restApi + '/things/active', opts)
      const activeThings = await res.json()

      const loginCredentialsProvider =
        'cognito-idp.' +
        AWS_CONFIG.region +
        '.amazonaws.com/' +
        AWS_CONFIG.userPoolId
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: AWS_CONFIG.identityPoolId,
        Logins: {
          [loginCredentialsProvider]: user.credentials.idToken,
        },
      })
      const Iot = new AWS.Iot(user.credentials)
      const IotAnalytics = new AWS.IoTAnalytics()
      const data = await getCsvShka(IotAnalytics, 'st_eye_dataset_laststatus')
      const dataUri = data['entries'][0]['dataURI']
      const dataset = await getCSV(dataUri)
      dispatch({
        type: DATA_FETCH,
        status: 'success',
        payload: {
          activeThings,
          dataset,
        },
      })
    } catch (error) {
      dispatch({
        type: DATA_FETCH,
        status: 'failure',
        payload: error,
      })
    }
  }
}
