import React from 'react'
import { connect } from 'react-redux'
import { fetchThingList } from '../actions/ThingListActions'

import DispatchThingList from '../components/DispatchThingList'
import { fetchAddressList } from '../actions/AddressListActions'

const mapStateToProps = state => ({
  thingListParams: state.dispatch.filter.params,
  thingList: state.thingList.data,
  thingListPending: state.thingList.dataPending,
  thingListError: state.thingList.dataError,
  addressList: state.addressList.list,
})

const mapDispatchToProps = dispatch => ({
  onFetchThingList: params => dispatch(fetchThingList(params)),
  onFetchAddressList: params => dispatch(fetchAddressList(params)),
})

class DispatchThingListContainer extends React.PureComponent {
  render() {
    return <DispatchThingList {...this.props} />
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DispatchThingListContainer)
